import React from 'react';
import CloseIcon from '@material-ui/icons/Close';
import ReactPlayer from "react-player";
import { makeStyles } from '@material-ui/core/styles';
import { AppBar, Dialog, DialogContent, IconButton, Toolbar, Typography } from '@material-ui/core';

const useStyles = makeStyles((theme) => ({
    backdrop: {
        zIndex: theme.zIndex.drawer + 1,
        color: '#fff',
        display: "grid",
        gridTemplateRows: "100% 1fr"
    },
    di: {
        backgroundColor: 'rgba(255, 255, 255, 0.01)',
        backdropFilter: 'blur(7px)',
    },
    video: {
        display: "flex",
        height: "100%",
        width: "100%"
    },
    dialog: {
        "& .MuiDialog-paper": {
            maxWidth: "90vw",
            width: "90vw",
            maxHeight: "90vh",
            height: "90vh",
            overflow: "hidden",
            borderRadius: 0,
            margin: 0,
        },
    },
    content: {
        width: "100%",
        height: "100%",
        position: "relative",
    },
    playerWrapper: {
        position: "absolute",
        top: 0,
        left: 0,
        right: 0,
        bottom: 0,
    },
}));

export default function VideoView({ data, open, handleClose }) {
    const classes = useStyles();
    console.log(data)

    let host = window.location.origin
    // host = "http://192.168.29.92:8001/stream?uri="
    console.log(host)
    const handleDialogClick = (event) => event.stopPropagation();

    return (
        <div>
            <Dialog fullScreen open={open} className={classes.di} onClose={handleClose} BackdropProps={{ onClick: handleDialogClick }}>
                <AppBar className={classes.appBar} color="transparent">
                    <Toolbar variant={'dense'}>
                        <IconButton edge="start" color="inherit" aria-label="close" onClick={handleClose}>
                            <CloseIcon />
                        </IconButton>
                        <Typography variant="h6" className={classes.title}>
                            {data.fileName}
                        </Typography>
                    </Toolbar>
                </AppBar>
                <DialogContent className={classes.di}>
                    <ReactPlayer url={host + data.uri} playing width="100%" height="100%" controls/>
                    {/* <video className={classes.video} src={host + data.uri} controls /> */}
                </DialogContent>

            </Dialog>
        </div>
    );
}
