import React, { useCallback, useContext, useState } from "react";


import {
    Backdrop,
    Button,
    CircularProgress,
    Container,
    CssBaseline,
    makeStyles,
    Snackbar,
    TextField,
    Typography
} from "@material-ui/core";
import { Redirect } from "react-router-dom";
import { Alert } from "@material-ui/lab";


const useStyles = makeStyles((theme) => ({
    app: {
        width: "100%",
        height: "100vh",
        flexDirection: 'column',
        backgroundSize: "cover",
        backgroundPosition: "center",
        backgroundAttachment: "fixed",
    },
    paper: {
        paddingTop: theme.spacing(2),
        padding: theme.spacing(4),
        display: "flex",
        flexDirection: "column",
        alignItems: 'center',
        backgroundColor: 'rgba(180, 180, 255, 0.01)',
        backdropFilter: 'blur(7px)',
        boxShadow:
            "0px 0px 30px 1px rgba(70,70,70,0.8)",
        borderRadius: 20,
        // borderLeft: 'solid 1px rgba(255, 255, 255, 0.3)',
        // borderTop: 'solid 1px rgba(255, 255, 255, 0.3)',
    },
    avatar: {
        margin: theme.spacing(1),

    },
    form: {
        width: '100%', // Fix IE 11 issue.
        marginTop: theme.spacing(1),
    },
    submit: {
        margin: theme.spacing(3, 0, 2),
    },
    item: {
        borderRadius: 10,
        margin: 10,
    },
    backdrop: {
        zIndex: theme.zIndex.drawer + 1,
        color: '#fff',
    },
    container: {
        paddingTop: 100,
        padding: theme.spacing(14),
    },
}));

export default function Login({ history }) {
    const classes = useStyles();
    const [hide, setHide] = useState(false);
    const [open,setOpen] = useState(false)
    const [password, setPassword] = useState(undefined);
    const [name, setName] = useState(undefined);
    const [user, setUser] = useState(null)
    const [msg, setMsg] = useState("")
    console.log(window.location)
    let host = window.location.host;
    host = "192.168.29.92:8001"

    const handleLogin = async () => {

        setHide(true);
        if (name && password) {
            const params = {
                "username": name,
                "email": "",
                "password": password,
                "browserName": "client",
                "browserVersion": "22",
                "osName": "22",
                "osVersion": "33",
                "isMobile": false,
                "token": ""
            }


            const socket = new WebSocket("ws://"+host+"/auth?auth=1");

            socket.addEventListener('open', function (event) {

                socket.send(JSON.stringify(params));
                console.log("open")
                setMsg("Accept in the app to login")
                setOpen(true)
            });

            socket.addEventListener('message', function (event) {

                console.log('Received message from server:', event.data);
                const data = JSON.parse(event.data)
                if (data.status === "AUTHENTICATED") {
                    localStorage.setItem('token', data.token);
                    setUser(data)
                    setMsg("AUTHENTICATED")
                    setOpen(true)
                }
            });

            socket.addEventListener('close', function (event) {
                console.log('Connection closed:', event);
                if (event.reason === "rejected") {
                    setHide(false);
                    setMsg("You rejected")
                    setOpen(true)
                }
                if (event.reason === "invalid_password") {
                    setHide(false);
                    setMsg("Wrong credentials")
                    setOpen(true)
                }
                
            });
        }
    };
    const handleClose = (event, reason) => {
        if (reason === 'clickaway') {
          return;
        }
    
        setOpen(false);
      };

    if (user) {
        return <Redirect to="/" />;
    }
    return (
        <div className={classes.app}>
            <CssBaseline />
            <Container maxWidth="sm" className={classes.container}>

                <div className={classes.paper}>
                    <Typography>
                        Login
                    </Typography>
                    <div className={classes.form}>
                        <TextField
                            variant="outlined"
                            margin="normal"
                            required
                            size="small"
                            fullWidth
                            label="Username"
                            onChange={(e) => setName(e.target.value)}
                            autoFocus
                        />
                        <TextField
                            variant="outlined"
                            margin="normal"
                            required
                            size="small"
                            fullWidth
                            onChange={(e) => setPassword(e.target.value)}
                            label="Password"
                            type="password"
                        />

                        <Button
                            type="submit"
                            fullWidth
                            variant="contained"
                            color="primary"
                            onClick={handleLogin}
                            className={classes.submit}
                        >
                            LogIn

                        </Button>
                    </div>
                </div>

                <Backdrop className={classes.backdrop} open={hide}>
                    <CircularProgress color="secondary" />
                </Backdrop>
                <Snackbar open={open} autoHideDuration={6000} onClose={handleClose}>
                    <Alert onClose={handleClose} severity="success">
                        {msg}
                    </Alert>
                </Snackbar>

            </Container>
        </div>
    );
}